.card {
  background: none;
  visibility: hidden;
  width: 45rem;
  height: 50rem;
  background-image: url("../assets/card-default.png");
  transform: scale(0.8);
  margin-left: 18%;
  border: none;

  img {
    margin-top: 10rem;
    margin-left: 25rem;
    height: 13%;
    width: 14%;
    transform: scale(2.5);
    border: none;
  }

  .top-content {
    height: 2rem;
    width: 65%;
    margin-left: 11rem;
    display: flex;
    justify-content: space-between;
    margin-top: 0px;

    .top-left {
      text-transform: capitalize;
    }

    .top-right {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      width: 8rem;
    }
  }

  .middle-content {
    background: rgba(157, 164, 23, 0.51);
    height: 2rem;
    width: 60%;
    margin: 6.3rem 0 0 12.5rem;
    display: flex;
    justify-content: space-evenly;
    border: 1px solid rgb(9, 88, 31);
  }

  .bottom-content {
    height: 30%;
    width: 60%;
    margin-top: 3rem;
    margin-left: 15rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h2 {
      font-size: larger;
    }
  }
}
