.pokemons {
  .pokemon-name {
    text-align: center;

    input[type="text"] {
      padding: 10px;
      border-radius: 40px;
      font-size: 1.3rem;
      border: 3px solid black;
      outline: none;
      transition: 0.4s;
      text-align: center;
      width: 400px;
      margin-bottom: 20px;

      &:focus {
        background: black;
        border-radius: 4px;
        color: white;
      }
    }
  }

  .optionTab {
    justify-content: space-around;
    align-items: center;
    margin: 6rem;
    margin-bottom: 20px;
    background: darken($my_blue, 12);
    border-radius: 20px;
    padding: 3px;
    color: white;
    box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.452);

    input {
      transform: translateY(1px);
      cursor: pointer;
    }

    label {
      margin-left: 10px;
      margin-right: 10px;
      cursor: pointer;
    }
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
  }
}
